"use client"

import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
} from "chart.js"
import { Bar } from "react-chartjs-2"
import { ForensicResponse } from "../types/forensic"

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
)

export default function RiskAnalytics({
  data
}: {
  data: ForensicResponse
}) {
  const suspicious = data.suspicious_accounts

  const low = suspicious.filter(a => a.suspicion_score <= 40).length
  const medium = suspicious.filter(
    a => a.suspicion_score > 40 && a.suspicion_score <= 60
  ).length
  const high = suspicious.filter(a => a.suspicion_score > 60).length

  const patternCounts: Record<string, number> = {}

  suspicious.forEach(acc => {
    acc.detected_patterns.forEach(p => {
      patternCounts[p] = (patternCounts[p] || 0) + 1
    })
  })

  const chartData = {
    labels: ["Low (0â€“40)", "Medium (40â€“60)", "High (60+)"],
    datasets: [
      {
        label: "Accounts",
        data: [low, medium, high],
        backgroundColor: ["#60a5fa", "#ffcc00", "#ff3b3b"]
      }
    ]
  }

  const patternData = {
    labels: Object.keys(patternCounts),
    datasets: [
      {
        label: "Pattern Frequency",
        data: Object.values(patternCounts),
        backgroundColor: "#6366f1"
      }
    ]
  }

  return (
    <div className="bg-[#1e293b] p-6 rounded-xl shadow-lg space-y-8">
      <h2 className="text-lg font-semibold text-white">
        Risk Analytics Overview
      </h2>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        <div>
          <Bar data={chartData} />
        </div>

        <div>
          <Bar data={patternData} />
        </div>
      </div>
    </div>
  )
}
