"use client"

import { useState } from "react"
import UploadPanel from "../components/UploadPanel"
import GraphView from "../components/GraphView"
import AccountDrawer from "../components/AccountDrawer"
import RingTable from "../components/RingTable"
import { ForensicResponse } from "../types/forensic"
import RiskAnalytics from "../components/RiskAnalytics"


export default function Home() {
  const [data, setData] = useState<ForensicResponse | null>(null)
  const [selectedAccount, setSelectedAccount] = useState<string | null>(null)
  const [selectedRing, setSelectedRing] = useState<string | null>(null)
  const [searchQuery, setSearchQuery] = useState("")
  const [suspiciousOnly, setSuspiciousOnly] = useState(false)

  return (
    <main className="min-h-screen bg-[#0f172a] text-gray-200 p-8 space-y-8">
      
      {/* Header */}
      <div>
        <h1 className="text-4xl font-bold text-white">
          Financial Forensics Engine
        </h1>
        <p className="text-gray-400 mt-2">
          Graph-Based Financial Crime Detection System
        </p>
      </div>

      {/* Upload Section */}
      <div className="bg-[#1e293b] p-6 rounded-xl shadow-lg">
        <UploadPanel
          onData={(response: ForensicResponse) => {
            setData(response)
            setSelectedAccount(null)
            setSelectedRing(null)
          }}
        />
      </div>

      {data && (
        <>
          {/* Summary Cards */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <div className="bg-[#1e293b] p-6 rounded-xl shadow-lg">
              <h2 className="text-sm text-gray-400">Total Accounts</h2>
              <p className="text-2xl font-semibold text-white">
                {data.summary.total_accounts_analyzed}
              </p>
            </div>

            <div className="bg-[#1e293b] p-6 rounded-xl shadow-lg">
              <h2 className="text-sm text-gray-400">
                Suspicious Accounts
              </h2>
              <p className="text-2xl font-semibold text-orange-400">
                {data.summary.suspicious_accounts_flagged}
              </p>
            </div>

            <div className="bg-[#1e293b] p-6 rounded-xl shadow-lg">
              <h2 className="text-sm text-gray-400">
                Fraud Rings Detected
              </h2>
              <p className="text-2xl font-semibold text-red-400">
                {data.summary.fraud_rings_detected}
              </p>
            </div>
          </div>
          <RiskAnalytics data={data} />


          {/* Controls */}
          <div className="bg-[#1e293b] p-4 rounded-xl shadow-lg flex flex-wrap gap-6 items-center">
            <div className="flex items-center gap-2">
              <input
                type="text"
                placeholder="Search account..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="bg-[#0f172a] border border-gray-600 px-3 py-2 rounded-md text-gray-200"
              />
              <button
                onClick={() =>
                  setSelectedAccount(searchQuery.trim().toUpperCase())
                }
                className="bg-blue-600 hover:bg-blue-700 px-4 py-2 rounded-md text-white"
              >
                Go
              </button>
            </div>

            <div className="flex items-center gap-2">
              <input
                type="checkbox"
                checked={suspiciousOnly}
                onChange={() => setSuspiciousOnly(!suspiciousOnly)}
              />
              <span className="text-gray-300">
                Show Suspicious Only
              </span>
            </div>
          </div>

          {/* Ring Table */}
          <RingTable
            rings={data.fraud_rings}
            selectedRing={selectedRing}
            onSelectRing={setSelectedRing}
          />

          {/* Graph */}
          <div className="bg-[#1e293b] p-6 rounded-xl shadow-lg">
            <h2 className="text-lg font-semibold text-white mb-4">
              Transaction Network Visualization
            </h2>

            <GraphView
              data={data}
              suspiciousOnly={suspiciousOnly}
              highlightedAccount={selectedAccount}
              selectedRing={selectedRing}
              onNodeClick={(id) => setSelectedAccount(id)}
            />
          </div>

          {/* Processing Time */}
          <div className="text-sm text-gray-500">
            Processed in{" "}
            <span className="font-medium text-gray-300">
              {data.summary.processing_time_seconds}
            </span>{" "}
            seconds
          </div>

          {/* Drawer */}
          <AccountDrawer
            selectedAccount={selectedAccount}
            data={data}
            onClose={() => setSelectedAccount(null)}
          />
        </>
      )}
    </main>
  )
}
